import React from 'react';
import { LandingPage } from './components/LandingPage/LandingPage';
import { Theme, presetGpnDark, presetGpnDefault } from '@consta/uikit/Theme';
import { MainPage } from './components/MainPage/MainPage';
import styles from './styles.module.css';
import './variables.css';
import { createRef, useRef, useState } from 'react';

export const App = () => {
  const main = createRef();
  const landing = createRef();
  const mainChild = useRef();

  function handleClick() {
    if (main.current.style.display === 'block') {
      main.current.style.display = 'none';
      landing.current.style.display = 'block';
    } else {
      main.current.style.display = 'block';
      landing.current.style.display = 'none';
      mainChild.current.openChat();
    }
  }
  function handleCabinetClick() {
    main.current.style.display = 'block';
    landing.current.style.display = 'none';
    mainChild.current.openCabinet();
  }

  //let theme = 'Dark';
  const [theme, setTheme] = useState(presetGpnDark);

  function handleTheme() {
    if (theme === 'Dark') return presetGpnDark;
    if (theme === 'Default') return presetGpnDefault;
  }

  return (
    <Theme preset={theme}>
      <Theme preset={presetGpnDark}>
        <LandingPage
          onClick={handleClick}
          onClickCabinet={handleCabinetClick}
          innerRef={landing}
        />
      </Theme>
      <MainPage
        onClick={handleClick}
        innerRef={main}
        ref={mainChild}
        theme={theme}
        setTheme={setTheme}
      />
    </Theme>
  );
};
