import { ThemeToggler } from '@consta/uikit/ThemeToggler';
import { IconSun } from '@consta/icons/IconSun';
import { IconMoon } from '@consta/icons/IconMoon';
import { Theme, presetGpnDefault, presetGpnDark } from '@consta/uikit/Theme';
import { useState } from 'react';

const items = ['Dark', 'Default'];

const getItemIcon = (item) => {
  if (item === 'Dark') return IconMoon;
  if (item === 'Default') return IconSun;
};

const getTheme = (item) => {
  if (item === 'Dark') return presetGpnDark;
  if (item === 'Default') return presetGpnDefault;
};

const changeVars = (value, setTheme) => {
  const html = document.querySelector('html');
  if (value === 'Dark') {
    setTheme(presetGpnDark);
    html.style.setProperty('--main-background-color', '#000227');
    html.style.setProperty('--chat-background-color', '#222336');
    html.style.setProperty('--mion-message-color', '#353648');
    html.style.setProperty(
      '--user-message-color',
      'linear-gradient(0deg, rgba(0, 2, 39, 0.08), rgba(0, 2, 39, 0.08)), #2B4578'
    );
    html.style.setProperty('--chat-message-text-color', '#FFFFFF');
    html.style.setProperty('--sidebar-text-color', 'rgba(255, 255, 255, 0.6)');
    html.style.setProperty('--cabinet-title-color', '#FFFFFF');
    html.style.setProperty('--cabinet-profile-background-color', '#222336');
    html.style.setProperty('--cabinet-subtext2-color', '#999AA9');
    html.style.setProperty('--cabinet-files-border-color', '#494A5E');
    html.style.setProperty('--cabinet-ellipse-color', '#284072');
  }
  if (value === 'Default') {
    setTheme(presetGpnDefault);
    html.style.setProperty('--main-background-color', 'white');
    html.style.setProperty('--chat-background-color', '#F2F4F5');
    html.style.setProperty('--mion-message-color', '#FEFEFE');
    html.style.setProperty('--user-message-color', '#B3D7F2');
    html.style.setProperty('--chat-message-text-color', '#000227');
    html.style.setProperty('--sidebar-text-color', '#66677D');
    html.style.setProperty('--cabinet-title-color', '#000227');
    html.style.setProperty('--cabinet-profile-background-color', '#F2F4F5');
    html.style.setProperty('--cabinet-subtext2-color', '#66677D');
    html.style.setProperty('--cabinet-files-border-color', '#E6E7EA');
    html.style.setProperty('--cabinet-ellipse-color', '#B3D7F2');
  }
};

export const ThemeSwitcher = ({ setTheme }) => {
  const [value, setValue] = useState(items[0]);
  return (
    <Theme preset={getTheme(value)}>
      <ThemeToggler
        items={items}
        value={value}
        getItemKey={(item) => item}
        getItemLabel={(item) => item}
        getItemIcon={getItemIcon}
        // onChange={({ value }) => setValue(value)}
        onChange={({ value }) => {
          changeVars(value, setTheme);
          setValue(value);
        }}
        style={{ color: 'var(--sidebar-text-color)' }}
      />
    </Theme>
  );
};
