import { Layout } from '@consta/uikit/Layout';
import { Text } from '@consta/uikit/Text';
import styles from './styles.module.css';
import { Menu } from './Menu/Menu';
import { Chat } from './Chat/Chat';
import { Chats } from './Chats/Chats';
import { useState } from 'react';
import { Cabinet } from './Cabinet/Cabinet';
import { createRef, forwardRef, useImperativeHandle } from 'react';
import { MusicPlayer } from './MusicPlayer/MusicPlayer';
import { HeaderMenu } from './HeaderMenu/HeaderMenu';

let cabinet = false;
let chat = true;

export const MainPage = forwardRef(
  ({ onClick, innerRef, theme, setTheme }, ref) => {
    const [visibility, setVisibility] = useState(false);
    const chat = createRef();
    const cabinet = createRef();
    const music = createRef();

    function hideBlock() {
      if (!visibility) return { display: 'none' };
      else return { display: 'block' };
    }

    useImperativeHandle(
      ref,
      () => {
        return {
          openCabinet() {
            chat.current.style.display = 'none';
            cabinet.current.style.display = 'block';
            music.current.style.display = 'none';
          },
          openChat() {
            chat.current.style.display = 'block';
            cabinet.current.style.display = 'none';
            music.current.style.display = 'none';
          }
        };
      },
      [theme]
    );

    // useImperativeHandle(ref, () => ({
    //   openChat() {
    //     chat.current.style.display = 'block';
    //     cabinet.current.style.display = 'none';
    //   }
    // }));

    function clickCabinet() {
      //console.log(chat.current.style);
      //if (chat.current.style.display === 'block') {
      chat.current.style.display = 'none';
      cabinet.current.style.display = 'block';
      music.current.style.display = 'none';
      //}
    }

    function clickChat() {
      chat.current.style.display = 'block';
      cabinet.current.style.display = 'none';
      music.current.style.display = 'none';
    }

    function clickMusic() {
      music.current.style.display = 'block';
      chat.current.style.display = 'none';
      cabinet.current.style.display = 'none';
    }

    return (
      <div style={hideBlock()} ref={innerRef}>
        <Layout direction="row" className={styles.mainLayout}>
          <Layout direction="column" className={styles.sideBar}>
            <Layout flex={1}>
              {/* <div className={styles.chats}> */}
              <Chats
                clickChat={clickChat}
                clickMusic={clickMusic}
                theme={theme}
              />
              {/* </div> */}
            </Layout>
            <Layout flex={1}>
              <Menu onClick={onClick} clickCabinet={clickCabinet} />
            </Layout>
          </Layout>
          <Layout flex={3} className={styles.chats}>
            <Layout direction="column">
              <Layout>
                <HeaderMenu setTheme={setTheme} />
              </Layout>
              <Layout>
                <Cabinet innerRef={cabinet} setTheme={setTheme} />
                <Chat innerRef={chat} theme={theme} setTheme={setTheme} />
                <MusicPlayer innerRef={music} />
              </Layout>
            </Layout>
          </Layout>
          <div className={styles.rightBarrier}></div>
        </Layout>
      </div>
    );
  }
);
